import React, { useState } from 'react'

const Product = ({ name, type, brand, price, imageSrc }) =>{
  const [hdPicLoaded, setHdPicLoaded] = useState(false);
    const [mdPicLoaded, setMdPicLoaded] = useState(false);
  
  return (
    <div className="center-text">
      <div className="productName">{name}</div>
      <img
        onLoad={() => setHdPicLoaded(true)}
        src={imageSrc}
        style={{ display: hdPicLoaded ? "unset" : "none" }}
        alt="img"
        height="250"
        width="170"
        className="center"
      />
      <img
        onLoad={() => setMdPicLoaded(true)}
        src={imageSrc.replace("images", "images/lowQuality")}
        style={{ display: mdPicLoaded && !hdPicLoaded ? "unset" : "none" }}
        alt="img"
        height="250"
        width="170"
        className="center"
      />
     {
       (mdPicLoaded || hdPicLoaded) &&<>
      <div>Brand: {brand}</div>
      <div>Style: {type}</div>
      <div>Price: â‚¬{price}.00</div>
      </>
      }
    <span class="progressBar" style={{width: hdPicLoaded ? '100%' : mdPicLoaded ? '30%' : '10%'}}/>
    </div>
  );
}

export default Product;
